<template>
  <div class="single-menu-container">
    <div
      class="single-menu-item"
      :class="{ active: activeValue === item.value && activeSpaceId === item.spaceId }"
      :style="`font-size:${item.size || 14}px`"
      v-for="item in props.menus"
      :key="item.text"
      @click="handleMenuClick(item)"
      @mouseenter.stop.prevent="handleMouseenter(item)"
      @mouseleave.stop.prevent="handleMouseleave(item)"
    >
      <el-icon v-if="elIcon" :size="item.size || 16">
        <component :is="item.icon"/>
      </el-icon>
      <el-image style="width: 16px; height: 16px" :src="item.logoUrl" v-else>
        <template #error>
          <SvgIcon :name="item.icon" :size="item.size || 16"/>
        </template>
      </el-image>
      <span class="menu-item-text">{{ item.text }}</span>
      <el-tag class="ml-1" size="small" v-if="item.spaceType === 'PUBLIC'">全员</el-tag>
      <el-popover
        placement="bottom"
        :width="60"
        trigger="click"
        v-if="item.showMenu && showManageMenuValues.includes(item.value) && ['OWNER','ADMIN'].includes(userAuth.teamRole)"
        @show="handlePopoverShow"
        @hide="handlePopoverHide(item)"
        popper-class="menu-popover"
      >
        <template #reference>
          <el-button link style="margin-left: auto;height: 0px;" @click.stop>
            <SvgIcon name="menu-more"/>
          </el-button>
        </template>
        <el-button text style="width: 100%;" @click="handleAuthVisible(item.spaceId)">
          <SvgIcon name="menu-auth"/>
          <span>&nbsp;成员权限</span>
        </el-button>
        <el-button text style="margin-left: 0px;width: 100%;" @click="handleSetVisible(item)">
          <SvgIcon name="menu-setting"/>
          <span>&nbsp;空间设置</span>
        </el-button>
      </el-popover>
    </div>
  </div>
  <SimpleDialog
    v-model:visible="authData.visible"
    width="700px"
    :title="authData.title"
    :footer="false"
  >
    <AuthForm :spaceId="authData.spaceId" key="edit-auth"/>
  </SimpleDialog>
  <SimpleDialog
    v-model:visible="setData.visible"
    width="600px"
    :title="setData.title"
    :footer="false"
  >
    <SetForm :spaceItem="setData.spaceItem" @formResponse="handleSetRes" key="edit-auth"/>
  </SimpleDialog>
</template>

<script lang="ts">
//@ts-ignore
import {defineComponent} from 'vue'
import {
  Camera,
  Connection,
  Film,
  Menu,
  Money,
  Platform,
  Present,
  School,
  ShoppingBag,
  Tools
} from '@element-plus/icons-vue'

export default defineComponent({
  components: {
    Camera,
    Platform,
    ShoppingBag,
    Money,
    Menu,
    School,
    Tools,
    Film,
    Connection,
    Present
  }
})
</script>

<script setup lang="ts">
import {ref, watchEffect} from 'vue'
import {ElScrollbar} from 'element-plus'
import {MenuProps} from "./useSingleMenu";
import {MenuValueEnum, showManageMenuValues} from "@/enums/menuValueEnum";
import {useEmit} from "@/utils/useEmitter";
import AuthForm from './auth/AuthForm.vue'
import SetForm from './set/SetForm.vue'
import SimpleDialog from '@/components/SimpleDialog/SimpleDialog.vue'
import {useAuthForm} from "@/components/SingleMenu/auth/useAuthForm";
import {useSetForm} from "@/components/SingleMenu/set/useSetForm";
import {fetchTeamAndSpaceAuth} from "@/api/fetchs/team";
import {useTeamStore} from "@/store/modules/team";

const {authData, handleAuthVisible, handleAuthRes} = useAuthForm()
const {setData, handleSetVisible, handleSetRes} = useSetForm()

const props = defineProps<{
  menus: MenuProps[]
  elIcon?: boolean
  defaultValue?: MenuValueEnum
  defaultSpaceId?: string
}>()
const emit = defineEmits(['handleChange'])
const activeValue = ref<number | string | undefined>('')
const activeSpaceId = ref<string | undefined>('')
const isShowing = ref<boolean>(false)

const teamStore = useTeamStore()

const userAuth = ref<any>({
  uid: '',
  teamRole: ''
})

watchEffect(() => {
  activeValue.value = props.defaultValue
  activeSpaceId.value = props.defaultSpaceId
})

function handleMenuClick(menu) {
  if (activeValue.value === menu.value && activeSpaceId.value === menu.spaceId) {
    useEmit('handleUpdateListByPid', 'root')
  }
  activeValue.value = menu.value
  activeSpaceId.value = menu.spaceId

  emit('handleChange', menu)
}

function handleMouseenter(menu) {
  menu.showMenu = true
}

function handleMouseleave(menu) {
  if (!isShowing.value) {
    menu.showMenu = false
  }
}

function handlePopoverShow() {
  isShowing.value = true
}

function handlePopoverHide(menu) {
  menu.showMenu = false
  isShowing.value = false
}

/* ================================== fetchs ================================== */
async function handleGetTeamAndSpaceAuth() {
  const {data} = await fetchTeamAndSpaceAuth(teamStore.getActiveTeamId)
  userAuth.value.teamRole = data.teamRole
  userAuth.value.uid = data.uid
}

handleGetTeamAndSpaceAuth()

</script>


<style lang="scss">
.el-popover.menu-popover {
  padding: 4px;
  width: 130px;
  min-width: 130px;
}

</style>

<style lang="scss" scoped>
.single-menu-container {
  .single-menu-item {
    padding: 10px 0;
    cursor: pointer;
    display: flex;
    align-items: center;

    &:hover {
      color: var(--el-color-primary);
    }

    &.active {
      color: var(--el-color-primary);
    }

    .menu-item-text {
      margin-left: 10px;
    }
  }
}
</style>
