import {reactive} from 'vue'

export function useSetForm() {
  const setData = reactive({
    visible: false,
    title: '空间设置',
    type: 'add',
    spaceItem: {},
  })

  function handleSetVisible(spaceItem, text = '空间设置', type = 'add') {
    setData.type = type
    setData.title = text
    setData.visible = true
    setData.spaceItem = spaceItem
  }

  // 操作
  function handleSetRes(val) {
    if (val) {
      console.log('操作成功后...')
    }
    setData.visible = false
  }

  return {
    setData,
    handleSetVisible,
    handleSetRes
  }
}
