<template>
  <div class="auth-form-container" v-loading="loading">
    <el-table :data="tableData" empty-text="无数据" height="500" class="auth-table">
      <el-table-column prop="nickname" label="成员"/>
      <el-table-column prop="teamNickname" label="团队昵称"/>
      <el-table-column prop="joinTime" label="加入时间"/>
      <el-table-column width="160" align="right">
        <template #header>
          <el-input v-model="queryName" placeholder="搜索成员" :prefix-icon="Search" clearable @input="handleGetSpaceMemberPage()"/>
        </template>
        <template #default="scope">
          <el-popover
            placement="bottom"
            :width="180"
            trigger="click"
            popper-class="auth-popper"
            :disabled="disablePopover(scope.row.role)"
          >
            <template #reference>
              <el-button link>
                {{scope.row.role === 'OWNER' ? '拥有者' : scope.row.role === 'EDITOR' ? '可编辑' : scope.row.role === 'READER' ? '可查看' : '禁止访问'}}
                <el-icon class="el-icon--right">
                  <ArrowDown v-if="!disablePopover(scope.row.role)"/>
                </el-icon>
              </el-button>
            </template>
            <div
              :class="['mb-2','auth-btn',{'auth-btn-active':scope.row.role == item.role}]"
              v-for="item in spaceRoleInfos"
              :key="item.name"
              @click="handleSetSpaceMemeber(scope.row, item.role)"
            >
              <div class="auth-bth-first">{{item.name}}</div>
              <div class="auth-bth-second">{{item.desc}}</div>
            </div>
          </el-popover>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      v-model:current-page="currentPage"
      v-model:page-size="pageSize"
      background
      :page-sizes="[10, 20, 30, 40, 50]"
      layout="->, total, sizes, prev, pager, next"
      :total="totalCount"
      :pager-count="5"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
    />
  </div>
</template>

<script lang="ts" setup>
import {ArrowDown, Search} from '@element-plus/icons-vue'
import {computed, ref} from "vue";
import {fetchSetSpaceMemberRole, fetchSpaceMemberPage, fetchTeamAndSpaceAuth} from "@/api/fetchs/team";
import {SetSpaceMember} from "@/api/model/teamModel";
import {getSearchParams} from "@/utils/form";
import {ElMessage} from "element-plus/es";
import {useTeamStore} from "@/store/modules/team";
import {useAuthForm} from "@/components/SingleMenu/auth/useAuthForm";

const props = defineProps<{
  spaceId: string
}>()

const teamStore = useTeamStore()
const {spaceRoleInfos} = useAuthForm()

const loading = ref<boolean>(false)

const currentPage = ref(1)
const pageSize = ref(10)
const totalCount = ref<number>(0)
const queryName = ref('')

const form = ref<SetSpaceMember>({
  spaceId: props.spaceId,
  uid: '',
  role: ''
})

const handleSizeChange = (val: number) => {
  handleGetSpaceMemberPage()
}
const handleCurrentChange = (val: number) => {
  handleGetSpaceMemberPage()
}
const disablePopover = (role) => {
  return role === 'OWNER' || !userAuth.value.hasAuthToManageSpace
}

const tableData = ref<any[]>([])
const userAuth = ref<any>({
  teamRole: '',
  spaceRole: '',
  hasAuthToManageSpace: false
})

/* ================================== fetchs ================================== */

async function handleGetSpaceMemberPage() {
  try {
    loading.value = true
    const {rows, total} = await fetchSpaceMemberPage(currentPage.value, pageSize.value, props.spaceId, queryName.value)
    tableData.value = rows
    totalCount.value = total
  } finally {
    loading.value = false
  }
}

async function handleGetTeamAndSpaceAuth() {
  const {data} = await fetchTeamAndSpaceAuth(teamStore.getActiveTeamId, props.spaceId)
  userAuth.value.teamRole = data.teamRole
  userAuth.value.spaceRole = data.spaceRole
  userAuth.value.hasAuthToManageSpace = data.hasAuthToManageSpace
}

handleGetTeamAndSpaceAuth()
handleGetSpaceMemberPage()

async function handleSetSpaceMemeber(row, role) {
  if(row.role === role) {
    return
  }

  form.value.uid = row.uid
  form.value.role = role

  const params = getSearchParams(form.value)
  const {data} = await fetchSetSpaceMemberRole(params)

  ElMessage.success('设置成功')

  row.role = role
  row.joinTime = data?.joinTime || null
}
</script>

<style lang="scss">
.el-popover.auth-popper {
  padding: 4px;

  .auth-btn {
    padding: 6px 10px;
    border-radius: 8px;

    &:hover {
      background-color: #e9e9eb;
    }
  }

  .auth-btn-active {
    background-color: #e9e9eb;
  }

  .auth-btn-first {
    font-size: 13px;
    font-weight: 400;
    margin-bottom: 4px;
  }

  .auth-bth-second {
    font-size: 12px;
    color: #9aa5b8;
  }
}

</style>

<style lang="scss" scoped>
.auth-form-container {
  .auth-table {
    margin-bottom: 10px;
  }

  :deep(.el-pagination .el-select .el-input) {
    width: 100px;
  }
}
</style>
