<template>
  <div class="set-container">
    <el-tabs tab-position="left">
      <el-tab-pane label="基本信息">
        <ElForm
          ref="infoFormRef"
          :model="spaceForm"
          label-position="top"
          label-width="100px"
          size="large"
          class="login-basic-form"
        >
          <ElFormItem
            prop="name"
            label="空间名称"
            :rules="[{ required: true, message: '空间名称不能为空' }]"
          >
            <el-input
              v-model="spaceForm.name"
              placeholder="请输入空间名称"
              clearable
              maxlength="20"
              show-word-limit
            />
          </ElFormItem>
          <ElFormItem
            prop="name"
            label="空间图标"
            class="logo-image"
          >
            <el-upload
              v-loading="uploadLoading"
              class="avatar-uploader"
              :action="action + '?spaceId=' + spaceForm.spaceId"
              :show-file-list="false"
              accept=".jpg,.jpeg,.png,.JPG,.JPEG"
              :on-success="handleUploadSuccess"
              :before-upload="beforeUpload"
            >
              <el-image style="width: 40px;height: 40px;" :src="spaceForm.logoUrl">
                <template #error>
                  <SvgIcon :name="spaceItem.icon" :size="40"/>
                </template>
              </el-image>
            </el-upload>
          </ElFormItem>
          <ElFormItem
            prop="desc"
            label="空间描述"
          >
            <el-input
              v-model="spaceForm.desc"
              placeholder="请输入空间描述"
              type="textarea"
              clearable
              rows="4"
              resize="none"
              maxlength="128"
              show-word-limit
            />
          </ElFormItem>
          <div class="flex justify-end mt-10">
            <el-button type="primary" @click="handleSure">确定</el-button>
            <el-button @click="handleClose">取消</el-button>
          </div>
        </ElForm>
      </el-tab-pane>
      <el-tab-pane label="安全设置">
        <ElForm
          ref="ruleFormRef"
          label-position="top"
          label-width="100px"
          size="large"
          class="login-basic-form"
        >
          <ElFormItem
            prop="name"
            label="显示水印"
          >
            <el-form-item>
              <el-switch v-model="showWatermark" size="small" @change="handleSetSpaceWatermark"/>&nbsp;&nbsp;访问水印(功能测试中)
              <div style="line-height: 20px;margin-left: 40px;font-size: 13px;color: #9aa5b8;">勾选后，访问空间时会显示当前访客姓名和navigationline账号ID后6位，防止用户截屏或拍照泄密。</div>
            </el-form-item>
          </ElFormItem>
        </ElForm>
      </el-tab-pane>
      <el-tab-pane label="删除空间" v-if="props.spaceItem.spaceType !== 'PUBLIC'">
        <div style="padding: 10px;">
          <div style="font-size: 13px;color: #9aa5b8;margin-bottom: 10px;">
            空间：<b style="color: #F56C6C">{{spaceName}}</b> 将被解散，相关的信息将会被永久删除。这是一个不可恢复的操作，请谨慎对待！
          </div>
          <div style="display: flex;gap: 10px;">
            <el-input v-model.trim="confirmSpaceName" size="large" placeholder="请输入当前空间名称进行删除"></el-input>
            <el-button size="large" type="danger" @click="handleRemoveSpace">删除空间</el-button>
          </div>
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script setup lang="ts">
import {h, ref} from "vue";
import {MenuProps} from "@/components/SingleMenu/useSingleMenu";
import {fetchRemoveSpace, fetchSetSpaceInfo, fetchSetSpaceWatermark} from "@/api/fetchs/team";
import {getSearchParams} from "@/utils/form";
import {ElMessage} from "element-plus/es";
import {ElMessageBox} from "element-plus";
import {useEmit} from "@/utils/useEmitter";
import {useTeamStore} from "@/store/modules/team";
import {MENU_MY_COMMON_USE} from "@/consts/menu";
import env from "@/utils/env/env";

const props = defineProps<{
  spaceItem: MenuProps
}>()

const teamStore = useTeamStore()

const emit = defineEmits(['formResponse'])

const infoFormRef = ref()
const spaceForm = ref<any>({
  name: props.spaceItem.text,
  desc: props.spaceItem.desc,
  spaceId: props.spaceItem.spaceId,
  logoUrl: props.spaceItem.logoUrl
})

const showWatermark = ref<boolean>(false)
const confirmSpaceName = ref<string>('')
const spaceName = ref<string>(props.spaceItem.text || '')

const uploadLoading = ref<boolean>(false)
const action = `${env.publicApi}/api/v1/file/uploadSpaceLogo`

function handleSure() {
  infoFormRef.value.validate(valid => {
    if (valid) {
      handleSetSpaceInfo()
    }
  })
}

function handleClose() {
  emit('formResponse', false)
}

function handleUploadSuccess(res) {
  spaceForm.value.logoUrl = res.data
  uploadLoading.value = false
}

function beforeUpload(file) {
  uploadLoading.value = true
  const isLt2M = file.size / 1024 / 1024 < 2
  if (!isLt2M) {
    ElMessage.error('图片不能超过2MB!')
  }
  return isLt2M
}

/* ================================== fetchs ================================== */
async function handleSetSpaceInfo() {
  const params = getSearchParams(spaceForm.value)
  const {data} = await fetchSetSpaceInfo(params)
  if(data) {
    useEmit('handleUpdateTeamSpaceList')
    ElMessage.success('设置成功')
    spaceName.value = spaceForm.value.name
  }
}

async function handleSetSpaceWatermark() {
  let json = {spaceId: props.spaceItem.spaceId, showWatermark: showWatermark.value}
  const params = getSearchParams(json)
  const {data} = await fetchSetSpaceWatermark(params)
  if(data) {
    ElMessage.success('设置成功')
  }
}

async function handleRemoveSpace() {
  if(!confirmSpaceName.value) {
    ElMessage.error('请输入空间名称')
    return
  }
  ElMessageBox({
    title: '删除空间？',
    message: h('p', null, [
      h('p', null, [
        h('span', null, '删除 '),
        h('i', null, [h('b', null, `${spaceName.value}`)]),
        h('span', null, ' 空间后，该空间下的所有数据都将被同步删除，且不可恢复！')
      ])
    ]),
    showCancelButton: true,
    cancelButtonText: '取消',
    confirmButtonText: '确认',
    draggable: true,
    beforeClose: (action, instance, done) => {
      if (action === 'confirm') {
        instance.confirmButtonLoading = true
        instance.confirmButtonText = 'Loading...'
        setTimeout(() => {
          done()
          setTimeout(() => {
            instance.confirmButtonLoading = false
          }, 300)
        }, 300)
      } else {
        done()
      }
    }
  }).then(async () => {
    let json = {spaceId: props.spaceItem.spaceId, name: confirmSpaceName.value}
    const params = getSearchParams(json)
    const {data} = await fetchRemoveSpace(params)
    if(data) {
      if(teamStore.getActiveMenu.spaceId == props.spaceItem.spaceId) {
        teamStore.setActiveMenu(MENU_MY_COMMON_USE)
      }
      useEmit('handleUpdateTeamSpaceList')
      ElMessage.success('删除成功')
      emit('formResponse', true)
    }
  })
}
</script>

