import {reactive, ref} from 'vue'

export interface AuthItemForm {
  uid: string
  auth: string
  isDel: boolean
}

export interface AuthForm {
  spaceId: string
  items: AuthItemForm[]
}

export function useAuthForm() {
  const spaceRoleInfos = ref<any>([
    {
      role: 'EDITOR',
      name: '可编辑',
      desc: '可新建、编辑、下载、移动'
    },
    {
      role: 'READER',
      name: '可查看',
      desc: '仅支持在线浏览'
    },
    {
      role: null,
      name: '禁止访问',
      desc: '无权访问此空间'
    }
  ])

  const authData = reactive({
    visible: false,
    title: '成员权限',
    type: 'add',
    spaceId: null
  })

  function handleAuthVisible(spaceId, text = '成员权限', type = 'add') {
    authData.type = type
    authData.title = text
    authData.visible = true
    authData.spaceId = spaceId
  }

  // 操作
  function handleAuthRes(val) {
    if (val) {
      console.log('操作成功后...')
    }
    authData.visible = false
  }

  return {
    spaceRoleInfos,
    authData,
    handleAuthVisible,
    handleAuthRes
  }
}
